  #sliderDiv{
    width: 100%;
    position: relative;
    left: 15%;
  }

  #slider {
    transform: scale(0.5, 0.5);
  }
  
  #slider .rs-handle  {
    background-color: transparent;
    border: 8px solid transparent;
    border-right-color: #DA4FD2;
    margin: -8px 0 0 14px !important;
  }
  #slider .rs-handle:before  {
    display: block;
    content: " ";
    position: absolute;
    height: 12px;
    width: 12px;
    background: #DA4FD2;
    right: -6px;
    bottom: -6px;
    border-radius: 100%;
  }
  #slider .rs-handle:after  {
    display: block;
    content: " ";
    width: 103px;
    position: absolute;
    top: -4px;
    right: 0px;
    border-top: 10px solid #DA4FD2;
  }
  #slider .rs-range-color  {
    background-color: #FFF;
  }
  #slider .rs-path-color  {
    background-color: #FFF;
  }
  
  #slider .rs-border {
    //border-color: #FFF;
  }

  #slider .rs-inner{
    background-color: #171941;
  }

  #slider .rs-block{
    padding: 5px !important;
    padding-right: 10px !important;
    padding-left: 4px !important;
  }
}

.index-page,
.landing-page,
.profile-page,
.user-page,
.register-page,
.login-page {
  background-image: url("/assets/img/dots.png");
  background-size: contain;
}

@include media-breakpoint-down(md) {
  .section:nth-of-type(2) {
    .path {
      left: 10% !important;
    }
    .path2 {
      left: 20% !important;
    }
  }

  .info:first-of-type {
    padding-top: 10% !important;
  }
}

@include media-breakpoint-down(sm) {
  .card-login img {
    width: 70%;
  }

  .section:nth-of-type(2) {
    .path,
    .path2 {
      left: 0 !important;
    }
  }
  .section-coins {
    margin-top: 0 !important;
  }
}

@include media-breakpoint-down(xs) {
  .card-login {
    margin-top: 100px;
    img {
      width: 350px;
    }
  }

  .profile-page .page-header {
    max-height: unset;
  }

  .landing-page {
    .page-header {
      h1 {
        margin-top: 100px;
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .info:first-of-type {
    padding-top: 60%;
  }
}
